  align-items: center;
  margin-left: auto;
  padding: 0;
  display: inline-flex;
  position: relative;
}

.menu-button:hover, .menu-button:focus {
  fill: #2196f3;
}

.menu-button .icon-close {
  display: none;
}

.menu-button.is-open .icon-close {
  display: block;
}

