    z-index: 9999;
    background: linear-gradient(0deg, #2086ff 15.1%, rgba(255, 255, 255, .92) 100%);
    padding-top: 25px;
    padding-bottom: 45px;
    font-size: 20px;
    transition: -webkit-transform .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 100%;
    left: 0;
    -webkit-transform: translateX(110%);
    transform: translateX(110%);
    box-shadow: 0 4px 8px rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .08), 0 1px 3px rgba(0, 0, 0, .16);
  }

  .menu-container.is-open {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }
}

